.navbar {
  position: fixed;
  width: 100%;
  margin: auto;
  z-index: 998;
  height: 10vh;
}

.navbar.active {
  background: transparent;
  backdrop-filter: blur(3px);
}
.dropmenu{
  display: flex;
}
.dropmenu.active{
  background-color: rgba(0, 0, 0, 0.6);
  height: 100vh;
}

.logo {
  flex: 2.5;
  padding: 20px;
  cursor:pointer;
}
.hamburger{
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.hamburger.active{
  background-color: rgba(0, 0, 0, 0.9);
  align-items: flex-start;
}

.sidebar {
  height: 0;
  overflow: hidden;

}

.sidebar.active {
  height: 100vh;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

li {
  list-style: none;
  padding: 10px;
  cursor:pointer;
  font-size: larger;
  font-weight: 500;
  color: white;
  text-align: center;
}




.dropmenu.active .bar:nth-child(2) {
  opacity: 0;
}

.dropmenu.active .bar:nth-child(1) {
  transform: translateY(8px) rotate(45deg);
  background-color: #ffffff;
}

.dropmenu.active .bar:nth-child(3) {
  transform: translateY(-8px) rotate(-45deg);
  background-color: #ffffff;
}

.bar {
  display: block;
  width: 25px;
  height: 3px;
  margin: 5px auto;
  /* -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out; */
  background-color: black;
  cursor: pointer;
}

.bars {
  cursor: pointer;
  padding: 20px;
}
.progress-bar {
  position: fixed;
  top: 9.8vh;
  left: 0;
  height: 1.5px;
  background: linear-gradient(to right, #ffffff 50%, #ffffff 50%, rgb(0, 0, 0));
  z-index: 999;
}
